body {
  margin: 0;
  font-family: "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: "Montserrat", sans-serif;
}

.container {
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
}
@media screen and (min-width: 320px) {
  .container {
    width: 320px;
    margin-left: auto;
    margin-right: auto;
  }
}
@media screen and (min-width: 768px) {
  .container {
    width: 704px;
  }
}
@media screen and (min-width: 1280px) {
  .container {
    width: 1098px;
  }
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

@font-face {
  font-family: "Montserrat";
  src: url("shared/fonts/Montserrat-SemiBold.eot?#iefix")
      format("embedded-opentype"),
    url("shared/fonts/Montserrat-SemiBold.woff") format("woff"),
    url("shared/fonts/Montserrat-SemiBold.ttf") format("truetype");
  font-weight: 600;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  src: url("shared/fonts/Montserrat-Medium.eot?#iefix")
      format("embedded-opentype"),
    url("shared/fonts/Montserrat-Medium.woff") format("woff"),
    url("shared/fonts/Montserrat-Medium.eot") format("truetype");
  font-weight: 500;
  font-style: normal;
}
